'use strict';

(function () {
  'use strict';

  angular.module('zion', ['module.advert', 'ui.router']).config(config);

  config.$inject = ['$locationProvider', '$urlRouterProvider', '$stateProvider'];
  function config($locationProvider, $urlRouterProvider, $stateProvider) {
    $locationProvider.hashPrefix('!');
    $urlRouterProvider.otherwise("/advert/index");
    if (!localStorage.adverts || JSON.parse(localStorage.adverts).length === 0) localStorage.adverts = JSON.stringify([{
      "name": "PHP Developer",
      "contact": { "tel": "011 222 5000", "email": "recruit@mail.co.za" },
      "description": "LAMP Developer",
      "start": "2016-06-12T22:00:00.000Z",
      "end": "2016-06-29T22:00:00.000Z"
    }]);
  }
})();
'use strict';

(function () {
  'use strict';

  angular.module('module.advert', ['ui.router', 'ngAnimate', 'ui.bootstrap']).config(config);

  config.$inject = ['$stateProvider'];
  function config($stateProvider) {
    $stateProvider.state('advert', {
      url: "/advert/",
      abstract: true,
      template: '<div ui-view></div>'
    }).state('advert.index', {
      url: 'index',
      templateUrl: 'views/advert/advert.index.html',
      controller: 'advertIndexController'
    }).state('advert.create', {
      url: 'create',
      templateUrl: 'views/advert/advert.create.html',
      controller: 'advertCreateController'
    });
  }
})();
'use strict';

(function () {
  'use strict';

  angular.module('module.advert').controller('advertIndexController', indexController).controller('advertCreateController', createController);

  indexController.$inject = ['$scope'];
  function indexController($scope) {
    $scope.models = JSON.parse(localStorage.adverts || '[]');
    $scope.call = {
      remove: remove
    };

    function remove(index) {
      if (!confirm("You are about to delete a record")) return;
      $scope.models.splice(index, 1);
      localStorage.adverts = JSON.stringify($scope.models);
    }
  }

  createController.$inject = ['$scope', '$state'];
  function createController($scope, $state) {
    $scope.property = { calendar: {}, errors: [] };
    $scope.model = {};
    $scope.call = {
      open: open,
      submit: submit
    };

    function open(name) {
      $scope.property.calendar[name] = true;
    }

    function submit(form) {
      if (form.$invalid) return $scope.property.errors = [{ detail: "Form is not valid" }];
      var models = JSON.parse(localStorage.adverts || '[]');
      models.push($scope.model);
      localStorage.adverts = JSON.stringify(models);
      $state.go('advert.index');
    }
  }
})();